export const apiUrl = "https://jgittins.alwaysdata.net/";
// export const apiUrl = "http://localhost:8888/";
function ajaxGet(url, callback) {
    var getReq = new XMLHttpRequest();
    getReq.addEventListener("load", callback);
    getReq.open("GET", apiUrl + url);
    getReq.send();
}
export function ajaxPost(url, callback, data) {
    var getReq = new XMLHttpRequest();
    getReq.addEventListener("load", callback);
    getReq.open("POST", apiUrl + url);
    getReq.setRequestHeader("Content-type", "text/plain");
    getReq.send(JSON.stringify(data));
}
export async function getGuid() {
    let guid = localStorage.getItem("pixelGuid");
    if (!guid) {
        ajaxGet("guid", function () {
            guid = this.responseText;
            if (guid) {
                localStorage.setItem("pixelGuid", guid);
            }
            return guid;
        });
    }
    return guid;
}
export function populateRecent(model) {
    ajaxGet("recent", function () {
        let data = LZString.decompressFromBase64(this.responseText);
        if (data) {
            let response = JSON.parse(data);
            if (Array.isArray(response)) {
                model.recent = response;
                model = model;
            }
        }
    });
}
export async function fetchy(url) {
    return fetch(apiUrl + url)
        .then((r) => r.text())
        .then((data) => {
        let dataString = LZString.decompressFromBase64(data);
        if (dataString) {
            return JSON.parse(dataString);
        }
    });
}
