<script lang="ts">
    import { msToTime, downloadSketch } from "./gamemodel";
    import { onMount, onDestroy } from "svelte";
    import { fetchy } from "./ajax";
    import { fly } from "svelte/transition";
    export let replyId : number;
    export let listType : string;
    export let watchReplay : (id : number) => void;
    export let reply : (id : number) => void;
    export let viewRepliesApp : (id : number) => void;
    export let x = 500;
    let showReplyLink = replyId == null;
    let list: any[] = [];
    let currTime = Date.now();
    let interval = setInterval(() => (currTime = Date.now()), 1000);

    onDestroy(function () {
        clearInterval(interval);
    });
    
    onMount(async () => {
        let url = listType;
        if (listType == 'replies')
            url += '/' + replyId;
        fetchy(url).then((r) => {
            list = r;
        })
    });
    function viewReplies(id: number) {
        showReplyLink = false;
        viewRepliesApp(id);
        fetchy("replies/" + id).then((r) => {
            list = r;
        })
    }
</script>

<div class="list-column page" in:fly="{{x:x, duration:500}}" out:fly="{{x:x, duration:500}}">
    {#each list as item}
        <div class="sketch frame" data-sketch-id={item.id}>
            <span
                class="time"
                data-time={item.timestamp}>{msToTime(currTime - item.timestamp)}
                ago</span>
            <img
                src={item.dataUrl}
                on:click={e => watchReplay(item.id)}
                alt="Sketch" />
            <a
                href={'javascript:void(0);'}
                class="download"
                title="download"
                on:click={e => downloadSketch(item.id)}>
                <i class="fas fa-download" />
            </a>
            {#if showReplyLink}
                {#if item.replies}
                    <a
                        href={'javascript:void(0);'}
                        class="replies"
                        on:click={e => viewReplies(item.id)}>{item.replies}
                        {item.replies > 1 ? ' replies' : ' reply'}</a>
                {:else}
                    <a
                        href={'javascript:void(0);'}
                        class="replies"
                        on:click={e => reply(item.id)}>doodle a reply</a>
                {/if}
            {/if}
        </div>
    {/each}
    <div class="clear"></div>
</div>
