import { writable } from 'svelte/store';
export function msToTime(duration) {
    let seconds = Math.floor((duration / 1000) % 60);
    let minutes = Math.floor((duration / (1000 * 60)) % 60);
    let hours = Math.floor((duration / (1000 * 60 * 60)) % 24);
    let days = Math.floor(duration / (1000 * 60 * 60 * 24));
    hours = (hours < 10) ? '0' + hours : hours;
    minutes = (minutes < 10) ? '0' + minutes : minutes;
    seconds = (seconds < 10) ? '0' + seconds : seconds;
    return (days > 0 ? days + 'd ' : '') + (hours > 0 ? hours + 'h ' : '') + minutes + 'm ' + seconds + 's';
}
export class Model {
    constructor() {
        this.recent = [];
        this.popular = [];
        this.replies = [];
    }
}
function downloadURI(uri, name) {
    var link = document.createElement("a");
    link.download = name;
    link.href = uri;
    document.body.append(link);
    link.click();
    link.remove();
}
export function downloadSketch(id) {
    let bg = '#eaead9';
    let width = 128 * 4;
    let height = 72 * 4;
    let canvas = document.createElement('canvas');
    canvas.width = width;
    canvas.height = height;
    let context = canvas.getContext('2d');
    context.imageSmoothingEnabled = false;
    context.fillStyle = bg;
    context.fillRect(0, 0, width, height);
    context.drawImage(document.querySelector('div.sketch[data-sketch-id="' + id + '"] img'), 0, 0, 128, 72, 0, 0, width, height);
    var myImage = canvas.toDataURL("image/png");
    canvas.remove();
    downloadURI("data:" + myImage, "doodle_" + id + ".png");
}
export const model = writable(new Model());
