<script lang="ts">
    import { fetchy } from "./ajax";
    export let replayId: number;
    export let stopReplay: () => void;

    let replayFrameId: number;

    function hideReplay() {
        if (replayFrameId) cancelAnimationFrame(replayFrameId);
        stopReplay();
    }

    let renderHistory = function (sketchHistory: any[]) {
        if (replayFrameId) cancelAnimationFrame(replayFrameId);

        let canvas = document.getElementById(
            "replay-canvas"
        ) as HTMLCanvasElement;
        let context = canvas.getContext("2d");
        context.fillStyle = "#000";
        let replayCounter = 0;
        context.clearRect(0, 0, canvas.width, canvas.height);
        let drawHistory = function () {
            if (replayCounter < sketchHistory.length) {
                context.beginPath();
                context.rect(
                    sketchHistory[replayCounter].x,
                    sketchHistory[replayCounter].y,
                    1,
                    1
                );
                context.fill();
            } else {
                cancelAnimationFrame(replayFrameId);
            }
            replayCounter++;
        };
        let updateReplay = function () {
            drawHistory();
            drawHistory();
            replayFrameId = requestAnimationFrame(updateReplay);
        };
        updateReplay();
    };

    fetchy("data/" + replayId).then((r) => {
        renderHistory(r);
    });
</script>

<div id="replay" on:click={hideReplay}>
    <div class="replay-container">
        <div class="frame">
            <canvas id="replay-canvas" width="128" height="72" />
        </div>
    </div>
</div>
