<script lang="ts">
    import { onMount } from "svelte";
    import { attachDrawingToCanvas } from "./drawing";
    import { fly } from 'svelte/transition';
    export let guid : string;
    export let replyId : number;
    export let postedDrawing : () => void;
    onMount(() => {
        attachDrawingToCanvas(document.getElementById('sketch-canvas') as HTMLCanvasElement, document.getElementById('replay-button'), guid, replyId, postedDrawing);
    })

</script>


<div id="create" class="page" in:fly="{{x:-500, duration:500}}" out:fly="{{x:-500, duration:500}}">
    <div class="toolbar">
      <a id="replay-button" href={"javascript:void(0);"} title="Replay"><i class="far fa-play-circle"></i></a>
      <a id="trash-button" href={"javascript:void(0);"} title="Trash"><i class="fas fa-trash"></i></a>
    </div>
    <div class="canvas-container">
      <div class="frame">
        <canvas id="sketch-canvas" width="128" height="72"></canvas>
      </div>
    </div>
    <div class="toolbar">
      <a id="post-button" href={"javascript:void(0);"} title="Post"><i class="fas fa-file-upload"></i></a>
    </div>
    <div class="clear"></div>
</div>