<script lang="ts">
	import { fly, fade } from 'svelte/transition';
	import { getGuid } from "./ajax";
	import Create from "./Create.svelte";
	import List from "./ListView.svelte";
	import Replay from "./Replay.svelte";
	let guid: string;
	getGuid().then((r) => (guid = r));
	enum lists {
		recent,
		popular,
		replies,
	}
	enum pages {
		create,
		list,
	}
	let currentList = lists.recent;
	let currentPage = pages.list;
	let replyId: number;
	let watchingReplay = false;
	let replayId: number;
	function viewRepliesApp(id: number) {
		replyId = id;
		currentList = lists.replies;
	}
	function startDrawing() {
		currentPage = pages.create;
	}
	function reply(id: number) {
		replyId = id;
		currentPage = pages.create;
	}
	function watchReplay(id: number) {
		watchingReplay = true;
		replayId = id;
	}
	function stopReplay() {
		watchingReplay = false;
	}
	function postedDrawing() {
		if (replyId) {
			currentList = lists.replies;
		} else {
			currentList = lists.recent;
		}
		currentPage = pages.list;
	}
</script>
<div class="top">
	<h1>Doodles</h1>
	{#if currentPage == pages.list}
		{#if currentList == lists.replies}
			<a href={'#'} on:click={(e) => reply(replyId)} class="big-link"><i
					class="fas fa-pen-square" />
				Reply</a>
		{:else}
			<a href={'#'} on:click={startDrawing} class="big-link"><i
					class="fas fa-pen-square" />
				Create Doodle</a>
		{/if}
	{:else}
		<a	
			href={'javascript:void(0);'}
			on:click={(e) => (currentPage = pages.list)}
			class="big-link"
			id="back-button"><i class="fas fa-backward" />
			Back</a>
	{/if}
</div>
<div class="page-container">
	{#if currentPage == pages.list}
		<div id="list-panel" class="list page" in:fly="{{x:500, duration:500}}" out:fly="{{x:500, duration:500}}">
			<div class="nav-buttons">
				<span class="big-link" class:active={currentList === lists.recent}>
					<a
						href={'javascript:void(0);'}
						on:click={(e) => {
							currentList = lists.recent;
							replyId = null;
						}}><i class="fas fa-bolt" />
						Recent Doodles</a>
					<span><i class="fas fa-bolt" /> Recent Doodles</span>
				</span>
				<span class="big-link" class:active={currentList === lists.popular}>
					<a href={'javascript:void(0);'}	on:click={(e) => {currentList = lists.popular;replyId = null;}}>
						<i class="fas fa-star" />
						Popular Doodles
					</a>
					<span><i class="fas fa-star" /> Popular Doodles</span>
				</span>
			</div>
			<div class="page-container">
				{#if currentList == lists.recent}
					<List listType="recent" {watchReplay} {reply} {viewRepliesApp} {replyId} x={-500}/>
				{/if}
				{#if currentList == lists.popular}
					<List listType="popular" {watchReplay} {reply} {viewRepliesApp} {replyId}/>
				{/if}
				{#if currentList == lists.replies}
					<List listType="replies" {watchReplay} {reply} {viewRepliesApp} {replyId} />
				{/if}
			</div>
		</div>
	{/if}
	{#if currentPage != pages.list}
		<Create {guid} {replyId} {postedDrawing} />
	{/if}
</div>
{#if watchingReplay}
	<Replay {replayId} {stopReplay} />
{/if}

